.editable-text-input {
  flex-grow: 1;
  min-width: 0px;

  background-color: unset;
  color: inherit;

  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 10px;
  padding-right: 10px;

  font: inherit;
  margin: 0px;
  border-width: 0px;
  border-radius: 0px;

  outline-color: hsla(0, 0%, 100%, 25%);
  outline-width: 1px;
  outline-offset: -1px;

  user-select: none;
}

.editable-text-input:focus {
  outline-style: solid;
}
