.entry-root {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: 4px;

  padding: 4px;
  cursor: pointer;
}

.entry-row {
  display: flex;
  align-items: center;
  gap: inherit;
}

.entry-row-vertical {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  gap: inherit;
}

.entry-counter {
  display: grid;
  grid-template-columns: 1fr 70px 1fr;
  justify-items: stretch;
  align-items: stretch;
  height: 50px;

  gap: 8px;
}

.entry-count {
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 1.25em;

  padding: 8px 12px;

  border-radius: 4px;
  border-width: 1px;
  border-style: solid;
}
